.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.App-header {
  background-color: #282c34;
  
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}



.contentContainer{
  margin-top:25px;
  border: 5px yellow dashed;
  height:90vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contentHeader{
  font-size: 30px;
}
.contentBody{
  border: black 2px dashed;
  height: 60vh;
  width: 90vw;
}

.columnContain{
  height: 60vh;
  border: green 2px dashed;
}

.footer{
  background-color: black; 
  color: white;
  height:5vh;
}

.sideColumnBody{
  border: blue solid 3px;
  height: 350px;
} 